@import 'variables';
@import 'mixins';

* {
  font-size: 16px;
  margin: 0;
  padding: 0;
}

html {
  font-size: 16px;
  font-size: 62.5%;
}

body {
  font-family: Helvetica, Arial, sans-serif;
  margin: 0;
  box-sizing: border-box;
  background-color: $colour-canvas-default;
}

/* Navigation Module */

#mainNav {
  .navbar-brand img {
    margin: 2.5rem 3.5rem;
    width: 150px;
    height: auto;
  }
}

.navbar-toggler-icon .fa-bars {
  font-size: 2.8rem;
}